<template>
  <div class="index">
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <div class="btn-block">
        <a download class="f-small main-c" href="../gy-files/template/教职工导入模板.xlsx">下载模板</a>
        <label for="importDom">
          <div class="btn-main btn-upload mr15 ml5">批量导入</div>
        </label>
        <input
            accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
            @change="upload($event)" ref="upload" id="importDom" type="file" hidden>
        <div @click="$exportDetail('/export/teacherInfoExport.do')"  class="btn-main mr15">导出</div>
        <div @click="$router.push('/personnel/teacher/addEdit')" class="btn-main-deepen">添加教职工</div>
      </div>
      <search-box :searchData="searchData" :table="table"></search-box>
      <el-table :data="table.list" class="el-table" border style="width: 100%">
        <el-table-column align="center" show-overflow-tooltip fixed prop="teacherName" label="姓名" width="90"></el-table-column>
        <el-table-column align="center" fixed label="性别" width="70">
          <template slot-scope="scope">
            <span>{{ $enums.Sex.getName(scope.row.sex) }}</span>
          </template>
        </el-table-column>
        <el-table-column align="center" show-overflow-tooltip fixed label="部门" width="170">
          <template slot-scope="scope">
            <span>{{scope.row.org.map(e=> e.name).join('，')}}</span>
          </template>
        </el-table-column>
        <el-table-column align="center" show-overflow-tooltip fixed prop="idCard" label="身份证号" width="170"></el-table-column>
        <el-table-column align="center" label="年龄" width="70">
          <template slot-scope="scope">
            <span>{{computedYear(scope.row.birthday)}}</span>
          </template>
        </el-table-column>
        <el-table-column align="center" label="职称" width="120">
          <template slot-scope="scope">
            <span>{{newestTile(scope.row.teacherTitleList)}}</span>
          </template>

        </el-table-column>
        <el-table-column align="center" label="评定时间" width="120">
          <template slot-scope="scope">
            <span>{{newestTime(scope.row.teacherTitleList)}}</span>
          </template>
        </el-table-column>
        <el-table-column align="center" prop="formationType" label="编制" width="90">
          <template slot-scope="scope">
            <span>{{ $enums.FormationType.getName(scope.row.formationType)}}</span>
          </template>
        </el-table-column>
        <el-table-column align="center" show-overflow-tooltip prop="teacherPosition" label="岗位" width="170">
          <template slot-scope="scope">
            <span>{{scope.row.teacherPosition}}</span>
          </template>
        </el-table-column>
        <el-table-column align="center" prop="folk" label="民族" width="90"></el-table-column>
        <el-table-column show-overflow-tooltip align="center" prop="residence" label="籍贯" width="90"></el-table-column>
        <el-table-column align="center" label="参加工作时间" width="120">
          <template slot-scope="scope">
            <span>{{scope.row.joinWorkTime | substrTime }}</span>
          </template>
        </el-table-column>
        <el-table-column align="center" label="政治面貌" width="90">
          <template slot-scope="scope">
            <span>{{ $enums.PoliticsStatus.getName(scope.row.politicsStatus) }}</span>
          </template>
        </el-table-column>
        <el-table-column align="center" label="加入时间" width="120">
          <template slot-scope="scope">
            <span>{{scope.row.joinPartyTime | substrTime }}</span>
          </template>
        </el-table-column>
        <el-table-column align="center" label="第一学历" width="120">
          <template slot-scope="scope">
            <span>{{ $enums.Education.getName(scope.row.firstEducation) }}</span>
          </template>
        </el-table-column>
        <el-table-column align="center" label="最高学历" width="120">
          <template slot-scope="scope">
            <span>{{ $enums.HighestEducation.getName(scope.row.highestEducation) }}</span>
          </template>
        </el-table-column>
        <el-table-column align="center" label="毕业时间" width="120">
          <template slot-scope="scope">
            <span>{{scope.row.graduateTime | substrTime }}</span>
          </template>
        </el-table-column>
        <el-table-column align="center" show-overflow-tooltip prop="graduateSchool" label="毕业学校" width="170"></el-table-column>
        <el-table-column align="center" label="来校时间" width="120">
          <template slot-scope="scope">
            <span>{{scope.row.joinSchoolTime | substrTime}}</span>
          </template>
        </el-table-column>
        <el-table-column align="center" label="校龄" width="70">
          <template slot-scope="scope">
            <span>{{ computedYear(scope.row.joinSchoolTime)}}</span>
          </template>
        </el-table-column>
        <el-table-column align="center" prop="joinPartyTime" label="加入时间" width="120">
          <template slot-scope="scope">
            <span>{{scope.row.joinPartyTime | substrTime }}</span>
          </template>
        </el-table-column>
        <el-table-column align="center" prop="phone" fixed="right" label="联系电话" width="120"></el-table-column>
        <el-table-column align="center" fixed="right" label="状态" width="90">
          <template slot-scope="scope">
            <span>{{ $enums.JobStatus.getName(scope.row.jobStatus) }}</span>
          </template>
        </el-table-column>
        <el-table-column align="center" fixed="right" label="操作" width="150">
          <template slot-scope="scope">
            <el-button type="text"
                       size="medium"
                       @click="goToDetail('detail',scope.row)"
            >详情
            </el-button>
            <el-button type="text"
                       size="medium"
                       @click="goToDetail('addEdit',scope.row)"
            >编辑
            </el-button>
            <el-button type="text"
                       size="medium"
                       @click="dimission(scope.row)"
            >离职
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <paging :data.sync="table"></paging>
    </main-part>
  </div>
</template>
<script>
  import mixins from '../../../assets/js/mixins'
  export default {
    mixins: [mixins.$exportDetail],
    data() {
      return {
        nav: [{name: "教职工管理", path: ""}],
        searchData: [
          {type: 'select', title: '性别', model: 'sex', option: this.$enums.Sex.list},
          {type: 'select', title: '部门', model: 'orgId', option: []},
          {type: 'select', title: '职称', model: 'teacherTitleType', option: this.$enums.TeacherTitleType.list},
          {type: 'select', title: '编制', model: 'formationType', option: this.$enums.FormationType.list},
          {type: 'cascader', title: '岗位', model: [], modelName: ['firstPosition', 'secondPosition'], option: []},
          {type: 'select', title: '民族', model: 'folk', option: []},
          {type: 'select', title: '政治面貌', model: 'politicsStatus', option: this.$enums.PoliticsStatus.list},
          {type: 'select', title: '第一学历', model: 'firstEducation', option: this.$enums.Education.list},
          {type: 'select', title: '最高学历', model: 'highestEducation', option: this.$enums.HighestEducation.list},
          {type: 'input', placeholder: '姓名/联系电话'}],
        table: {
          url: '/teacher/queryTeacherPage.do',
          el: '.el-table',
          total: 0,
          noData: true,
          list: [],
          query: {
            orgId: null,
            teacherTitleType: null,
            highestEducation: null,
            folk: null,
            politicsStatus: null,
            firstPosition: null,
            secondPosition: null,
            firstEducation: null,
            formationType: null,
            sex: null,
            keywords: null,
            pageNo: 1
          }
        },
        tableData: [{
          teacherName: '武帝刘彻',
          sex: '男',
          org: '语文教研组',
          idCard: '999999999999999999',
          birthday: 18,
          age: 1,
          formationType: 1,
          teacherPosition: 1,
          folk: 1,
          residence: 1,
          joinWorkTime: 1,
          politicsStatus: 1,
          joinPartyTime: 1,
          firstEducation: 1,
          highestEducation: 1,
          graduateTime: 1,
          graduateSchool: 1,
          joinSchoolTime: 1,
          schoolAge: 1,
          phone: 1,
          jobStatus: 1,
        }]
      }
    },
    methods: {
      // 离职
      async dimission(item) {
        await this.$confirm(`您确认将【${item.teacherName}】状态设为离职吗?`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
        await this.$ajax({
          url: '/teacher/dimissionTeacher.do',
          query: {
            id: item.id
          }
        })
        this.$remind('设为离职成功')
        this.$search(this.table)
      },
      // 前往教师详情
      goToDetail(type,item) {
        this.$router.push({
          path: `/personnel/teacher/${type}`, query: {
            id: item.id,
          }
        })
      },
      // 获取所有部门
      async getAllDep() {
        let res = await this.$ajax({
          url: '/org/queryOrganizationTree.do',
          query:{
            orgTypes: ['ADMIN_ORG','TEACH_ORG']
          }
        })
        this.searchData[1].option = res.subOrgList.map(e => {
          return {value: e.orgId, label: e.orgName}
        })
      },
      // 获取所有岗位
      async getPosition() {
        this.searchData[4].option = await this.$ajax({
          url: '/dict/queryTeacherPositionCascade.do'
        })
      },
      // 根据职称列表获取最新的职称
      newestTile(list){
        if(!list.length){
          return '--'
        }
        let date
        let index = 0
        // 获取第一个有时间的职称
        for(let i =0;i< list.length;i++) {
          if (list[i].acquireTime) {
            date = new Date(list[i].acquireTime).getTime()
            index = i
            break
          }
        }
        // 判断时间是否为最新
        for(let i =0;i< list.length;i++){
          if(!list[i].acquireTime){
            continue
          }
          if(date < new Date(list[i].acquireTime).getTime()){
            index = i
          }
        }
        return this.$enums.TeacherTitleType.getName(list[index].title)
      },
      // 根据职称列表获取最新的评定时间
      newestTime(list){
        if(!list.length){
          return '--'
        }
        let date
        let index = 0
        // 获取第一个有时间的职称
        for(let i =0;i< list.length;i++) {
          if (list[i].acquireTime) {
            date = new Date(list[i].acquireTime).getTime()
            index = i
            break
          }
        }
        for(let i =0;i< list.length;i++){
          if(!list[i].acquireTime){
            continue
          }
          if(date < new Date(list[i].acquireTime).getTime()){
            date = new Date(list[i].acquireTime).getTime()
          }
        }
        if(date){
          return  new Date(date).$Format('yyyy-MM-dd')
        }else{
          return '--'
        }
      },
      // 获取所有民族
      async getNationList() {
        let res = await this.$ajax({
          url: '/common/queryNationList.do'
        })
        this.searchData[5].option = res.map(e => {
          return {value: e.id, label: e.nation}
        })
      },
      // 导入
      async upload(e) {
        if (e.target.files.length === 0) return
        let file = e.target.files[0]
        this.$refs.upload.value = null;
        let formData = new FormData()
        formData.append('file', file)
        let obj = {
          url: '/import/importTeacherInfo.do',
          query: formData,
          el: '.btn-upload',
          noText: true
        }
        // 上传模板拿到返回的模板地址
        let res = await this.$ajax(obj)
        this.$remind('导入成功')
        this.$search(this.table)
      },
      // 计算年龄 工龄 校龄时间
      computedYear(val) {
        if (!val) {
          return '--'
        }
        let startDate = new Date(val).$Format('yyyy-MM-dd')
        let endDate = new Date().$Format('yyyy-MM-dd')
        let sy = parseInt(startDate.split('-')[0])
        let ey = parseInt(endDate.split('-')[0])
        let sm = parseInt(startDate.split('-')[1])
        let em = parseInt(endDate.split('-')[1])
        let sd = parseInt(startDate.split('-')[2])
        let ed = parseInt(endDate.split('-')[2])
        if (ed > sd) {
          em++
        } else {
          em--
        }
        if (em > sm) {
          ey++
        }
        let age = ey - sy - 1
        return age >= 0 ? age : 0
      }
    },
    mounted() {
      this.$search(this.table)
      this.getAllDep()
      this.getPosition()
      this.getNationList()
    }
  }
</script>
<style lang="sass" scoped>
  .index
    .btn-block
      .main-c
        vertical-align: top
        display: inline-block
        line-height: 30px
        margin-top: 2px
    /deep/ .el-table
      th
        padding: 12px 0
        background: $back-dark
        .cell
          color: $fc-def
          font-weight: bold
      td
        padding: 5px 0
</style>
